import React from 'react';
import { Link } from "react-scroll";
import navstyle from "./Navbarcss.module.css";
import FileSaver from 'file-saver';
export const NavbarMenu = () => {
  const saveFile = () => {
    FileSaver.saveAs(
      process.env.PUBLIC_URL + "/resources/Ravindra_Kumar_Resume.pdf",
      "fw20_1257-Ravindra-Kumar-Resume.pdf"
    );
    };
  return (
    <div>
     <nav id="nav-menu" className={navstyle.nav__container__actions}>
          <ul>
            <li className="nav-link home">
              <Link  activeClass={navstyle.active} smooth spy to="home">
               Home
              </Link>
            </li>
            <li className="nav-link about">
              <Link  activeClass={navstyle.active} smooth spy to="about">
             About Me
              </Link>
            </li>
            <li className="nav-link skills">
              <Link  activeClass={navstyle.active} smooth spy to="skills">
              Skills
              </Link>
            </li>
            <li className="nav-link projects" >
              <Link activeClass={navstyle.active} smooth spy to="projects">
             Projects
              </Link>
            </li>
            <li className="nav-link contact">
              <Link activeClass={navstyle.active} smooth spy to="contact">
               Contact
              </Link>
            </li>
            <li style={{color:"lightgray",
            boxShadow: "rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px"}}
            className="nav-link resume"
            id="resume-button-1"
       >
            <a
            id="resume-link-1"
            href="https://drive.google.com/file/d/1Kmr90IO6tKO1RLF6ebOWLNXMquIXzFqC/view?usp=sharing"
        target="_blank"
       rel="noopener"
       onClick={saveFile}
       style={{color: "lightgray",textDecoration:"none"}}
       >Resume</a>
            </li>
          </ul>
        </nav>

    </div>
  )
}

